<template>
  <form class="stack-small" @submit.prevent="onSubmit">
    <div>
      <label class="edit-label">Edit Name for &quot;{{ label }}&quot;</label>
      <input
        :id="id"
        ref="labelInput"
        type="text"
        autocomplete="off"
        v-model.lazy.trim="newLabel" />
    </div>
    <div class="btn-group">
      <el-button type="success" class="btn" @click="confirmCancel">
        Cancel
        <span class="visually-hidden">editing {{ label }}</span>
      </el-button>
      <el-button type="info" class="btn btn__primary" @click="confirmSave">
        Save
        <span class="visually-hidden">edit for {{ label }}</span>
      </el-button>
    </div>
  </form>
</template>

<script>
import { ElMessageBox } from 'element-plus';

export default {
  props: {
    label: {
      type: String,
      required: true,
    },
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      newLabel: this.label,
    };
  },
  methods: {
    onSubmit() {
      if (this.newLabel && this.newLabel !== this.label) {
        this.$emit("item-edited", this.newLabel);
        this.onCancel(); // 提交后也返回初始状态
      }
    },
    confirmCancel() {
      ElMessageBox.confirm('Are you sure you want to cancel?', 'Cancel Confirmation', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'No',
        type: 'warning'
      }).then(() => {
        this.onCancel(); // 用户确认取消
      }).catch(() => {
        // 用户取消操作
      });
    },
    confirmSave() {
      ElMessageBox.confirm('Are you sure you want to save changes?', 'Save Confirmation', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'No',
        type: 'info'
      }).then(() => {
        this.onSubmit(); // 用户确认保存
      }).catch(() => {
        this.onCancel(); // 用户选择不保存也返回初始状态
      });
    },
    onCancel() {
      // 取消逻辑
      this.newLabel = this.label; // 重置为初始值
      console.log('Cancelled editing');
    }
  },
  mounted() {
    const labelInputRef = this.$refs.labelInput;
    labelInputRef.focus();
  },
};
</script>

<style scoped>
  .edit-label {
    font-family: Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #0b0c0c;
    display: block;
    margin-bottom: 5px;
  }
  input {
    display: inline-block;
    margin-top: 0.4rem;
    width: 100%;
    min-height: 4.4rem;
    padding: 0.4rem 0.8rem;
    border: 2px solid #565656;
  }
  form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  form > * {
    flex: 0 0 100%;
  }
</style>